import Col from 'components/UI/Col';
import Content from 'components/UI/Content';
import Title from 'components/UI/Title';
import routes from 'helpers/routes';
import { useRouter } from 'next/router';
import React from 'react';
import { useSelector } from 'react-redux';
import PaymentByShares from './views/PaymentByShares';

import styles from './HelpCenter.module.scss';
import InstallmentPlan from './views/InstallmentPlan';
import Navbar from './views/Navbar';
import PaymentAndDelivery from './views/PaymentAndDelivery';
import Return from './views/Return';
import LoyaltyProgram from './views/LoyaltyProgram';

const tabList = [
	{
		id: 'paymentAndDelivery',
		name: 'Оплата и Доставка',
	},
	{
		id: 'return',
		name: 'возврат',
	},
	//{
	//	id: 'installmentPlan',
	//	name: 'Рассрочка Тинькофф',
	//},
	//{
	//	id: 'paymentByShares',
	//	name: 'Оплата долями',
	//},
	//{
	//	id: 'loyaltyProgram',
	//	name: 'Программа лояльности',
	//},
];

type activeTabType = 'paymentAndDelivery' | 'return' | 'installmentPlan' | 'paymentByShares' | 'loyaltyProgram';

type HelpCenterTypes = {
	currentTab: activeTabType;
};

const HelpCenter = ({ currentTab }: HelpCenterTypes) => {
	const [activeTab, setActiveTab] = React.useState<activeTabType>(currentTab);

	const isMobile = useSelector((state: any) => state.isMobile.isMobile);

	const router = useRouter();

	const changeActiveTab = (id: activeTabType) => {
		setActiveTab(id);
		router.push(
			{
				pathname: routes[id],
			},
			undefined,
			{ scroll: false }
		);
	};

	return (
		<Content className={styles.content}>
			<>
				<Title text="Help-центр" withSuisse size={isMobile ? 32 : 46} marginBottom={isMobile ? 40 : 80} />
				<Col className={styles.colContainer} marginBottom={isMobile ? 60 : 50}>
					<>
						<Navbar tabList={tabList} onClick={changeActiveTab} activeTab={activeTab} />
						{activeTab === 'paymentAndDelivery' && <PaymentAndDelivery />}
						{activeTab === 'return' && <Return />}
						{/*{activeTab === 'installmentPlan' && <InstallmentPlan />}
						{activeTab === 'paymentByShares' && <PaymentByShares />}*/}
						{/*{activeTab === 'loyaltyProgram' && <LoyaltyProgram />}*/}
					</>
				</Col>
			</>
		</Content>
	);
};

export default HelpCenter;
