import { SuisseIntl } from 'helpers/utils/fonts';
import React from 'react';

import styles from './Navbar.module.scss';

type NavbarTypes = {
	tabList: {
		id: string;
		name: string;
	}[];
	activeTab: string;
	onClick: (id: string) => void;
};

const Navbar = ({ tabList, activeTab, onClick }: NavbarTypes) => {
	return (
		<div className={styles.navbar}>
			{tabList.map(item => (
				<p
					key={item.id}
					id={item.id}
					onClick={() => onClick(item.id)}
					style={activeTab === item.id ? { opacity: '1' } : {}}
					className={SuisseIntl.className}
				>
					{item.name}
				</p>
			))}
		</div>
	);
};

export default Navbar;
